* {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
}

.app-wrapper {
  margin: 0 auto;
  display: grid;
  grid-gap: 10px;
  grid-template-areas:
    "h h"
    "n c"
    "f f";
  grid-template-columns: 2fr 10fr;
  grid-template-rows: 60px 1fr 50px;
}

.app-wrapper-content {
  grid-area: c;
  background-color: lightblue;
  padding: 10px;
}
